<template>
    <div>
        <div class="test"></div>
        <FootNavigationCarouselOption v-for="(contactInfo, index) in contactInfos" v-show="carouselChoose === index"
            :title="contactInfo.title" :address="contactInfo.address" />


        <div class="circulars">
            <template v-for="(contactInfo, index) in contactInfos">
                <div class="circular" @click="resetInterval(index)" v-show="carouselChoose !== index"></div>
                <div class="circular active" @click="resetInterval(index)" v-show="carouselChoose === index"></div>
            </template>
        </div>
    </div>
</template>

<script>
import FootNavigationCarouselOption from './FootNavigationCarouselOption.vue';
export default {
    name: 'FootNavigationCarousel',
    components: { FootNavigationCarouselOption },
    data() {
        return {
            carouselChoose: 0,
            contactInfos: [
                { title: '', address: ':1103:XXX@njfu.edu.cn' },
                { title: '', address: ':1110:XXX@njfu.edu.cn' },
                { title: 'Leo', address: '' }
            ]
        }
    },
    methods: {
        resetInterval(choose) {
            clearInterval(this.intervalID)
            this.carouselChoose = choose
            this.intervalID = setInterval(this.intervalFun, this.intervalSleep)
        }
    },
    mounted() {
        this.intervalFun = () => {
            if (this.carouselChoose === this.contactInfos.length - 1) this.carouselChoose = 0
            else this.carouselChoose++
        }
        this.intervalSleep = 4000
        this.intervalID = setInterval(this.intervalFun, this.intervalSleep)
    }
}
</script>

<style scoped>
.circulars {
    margin-top: 20px;
    text-align: center;
}

.circular {
    display: inline-block;
    width: 20px;
    height: 20px;
    border-radius: 100%;
    background-color: white;
    margin-left: 15px;
}

.circular:nth-child(-n + 2) {
    margin-left: 0;
}

.circular:hover {
    cursor: pointer;
}

.active {
    background-color: #00A78E;
}
</style>