// 
import VueRouter from 'vue-router'

// 
import NJFUWelcome from '../pages/NJFUWelCome.vue'
import NJFULogined from '../pages/NJFULogined.vue'
import NJFUIndex from '../pages/NJFUIndex.vue'
import NJFUIndexProfessionalIntroduction from '../pages/NJFUIndexProfessionalIntroduction.vue'
import NJFUIndexTeachers from '../pages/NJFUIndexTeachers.vue'
import NJFUIndexProfessionalUpdates from '../pages/NJFUIndexProfessionalUpdates.vue'
import NJFUIndexContactUs from '../pages/NJFUIndexContactUs.vue'
import NJFUFileShow from '../pages/NJFUFileShow.vue'
import NJFUSpace from '../pages/NJFUSpace.vue'
import NJFUTeachingResearch from '../pages/NJFUTeachingResearch.vue'
import NJFUMesRelease from '../pages/NJFUMesRelease.vue'
import NJFUViewerMes from '../pages/NJFUViewerMes.vue'
import NJFUViewerPDF from '../pages/NJFUViewerPDF.vue'
import NJFUViewerVideo from '../pages/NJFUViewerVideo.vue'
import NJFUCourseResources from '../pages/NJFUCourseResources.vue'
import NJFUSpaceUserInfo from '../pages/NJFUSpaceUserInfo.vue'
import NJFUSpaceFileUpload from '../pages/NJFUSpaceFileUpload.vue'
import NJFULoginedIndex from '../pages/NJFULoginedIndex.vue'
import NJFUSpaceHome from '../pages/NJFUSpaceHome.vue'

// 
const router = new VueRouter({
    routes: [
        {
            name: 'Welcome',
            path: '/',
            component: NJFUWelcome,
            meta: { title: '', toVisit: 'WelcomeIndex' },
            children: [
                {
                    name: 'WelcomeIndex',
                    path: 'index',
                    component: NJFUIndex,
                    meta: { title: '' }
                },
                {
                    name: 'WelcomeProfessionalIntroduction',
                    path: 'professionalIntroduction',
                    component: NJFUIndexProfessionalIntroduction,
                    meta: { title: '' }
                },
                {
                    name: 'WelcomeTeachers',
                    path: 'teachers',
                    component: NJFUIndexTeachers,
                    meta: { title: '' }
                },
                {
                    name: 'WelcomeProfessionalUpdates',
                    path: 'professionalUpdates',
                    component: NJFUIndexProfessionalUpdates,
                    meta: { title: '' }
                },
                {
                    name: 'WelcomeContactUs',
                    path: 'contactUs',
                    component: NJFUIndexContactUs,
                    meta: { title: '' }
                }
            ]
        },
        {
            name: 'Logined',
            path: '/logined',
            component: NJFULogined,
            meta: { title: '', toVisit: 'Index' },
            children: [
                {
                    name: 'LoginedIndex',
                    path: 'index',
                    component: NJFULoginedIndex,
                    meta: { title: '', toVisit: 'Index' },
                    children: [
                        {
                            name: 'Index',
                            path: 'index',
                            component: NJFUIndex,
                            meta: { title: '', isAuth: true }
                        },
                        {
                            name: 'ProfessionalIntroduction',
                            path: 'professionalIntroduction',
                            component: NJFUIndexProfessionalIntroduction,
                            meta: { title: '', isAuth: true }
                        },
                        {
                            name: 'Teachers',
                            path: 'teachers',
                            component: NJFUIndexTeachers,
                            meta: { title: '', isAuth: true }
                        },
                        {
                            name: 'ProfessionalUpdates',
                            path: 'professionalUpdates',
                            component: NJFUIndexProfessionalUpdates,
                            meta: { title: '', isAuth: true }
                        },
                        {
                            name: 'ContactUs',
                            path: 'contactUs',
                            component: NJFUIndexContactUs,
                            meta: { title: '', isAuth: true }
                        }
                    ]
                },
                {
                    name: 'FileShow',
                    path: 'fileShow',
                    component: NJFUFileShow,
                    meta: { title: '', isAuth: true },
                    props({ query: { fileShowID, fileShowTitle } }) { return { fileShowID, fileShowTitle } }
                },
                {
                    name: 'CourseResources',
                    path: 'courseResources',
                    component: NJFUCourseResources,
                    meta: { title: '', isAuth: true },
                    props({ query: { CourseResourcesID, CourseResourcesTitle } }) { return { CourseResourcesID, CourseResourcesTitle } }
                },
                {
                    name: 'TeachingResearch',
                    path: 'teachingResearch',
                    component: NJFUTeachingResearch,
                    meta: { title: '', isAuth: true }
                },
                {
                    name: 'MesRelease',
                    path: 'mesRelease',
                    component: NJFUMesRelease,
                    meta: { title: '', isAuth: true },
                    props({ query: { MesReleaseID, MesReleaseTitle } }) { return { MesReleaseID, MesReleaseTitle } }
                },
                {
                    name: 'ViewerMes',
                    path: 'viewerMes',
                    component: NJFUViewerMes,
                    meta: { title: '', isAuth: true },
                    props({ query: { mesID, mesTitle } }) { return { mesID, mesTitle } }
                },
                {
                    name: 'ViewerPDF',
                    path: 'viewerPDF',
                    component: NJFUViewerPDF,
                    meta: { title: 'PDF', isAuth: true },
                    props({ query: { PDFID, PDFTitle } }) { return { PDFID, PDFTitle } }
                },
                {
                    name: 'ViewerVideo',
                    path: 'viewerVideo',
                    component: NJFUViewerVideo,
                    meta: { title: '', isAuth: true },
                    props({ query: { videoID, videoTitle } }) { return { videoID, videoTitle } }
                },
                {
                    name: 'Space',
                    path: 'space',
                    component: NJFUSpace,
                    meta: { title: '', toVisit: 'SpaceHome' },
                    children: [
                        {
                            name: 'SpaceHome',
                            path: 'home',
                            component: NJFUSpaceHome,
                            meta: { title: '', isAuth: true }
                        },
                        {
                            name: 'SpaceFileUpload',
                            path: 'fileUpload',
                            component: NJFUSpaceFileUpload,
                            meta: { title: '', isAuth: true }
                        }
                    ]
                }
            ]
        }
    ]
})

router.setted = false

// -,
router.beforeEach((to, _, next) => {
    console.log(to)
    if (to.meta.toVisit) return router.replace({ name: to.meta.toVisit }, _ => { })

    // ,
    if (!router.setted) {
        router.setted = true
        router.app.$options.store.dispatch('userMesOptions/autoLogin', { to })
        return
    }

    if (!to.meta.isAuth && !router.app.$options.store.state.userMesOptions.is_login)
        next()

    if (to.meta.isAuth && router.app.$options.store.state.userMesOptions.is_login)
        next()
})

// -,
router.afterEach((to) => {
    document.title = to.meta.title || 'NJFU'
})

export default router