<template>
    <div class="positionRelative">
        <div class="IndexHeadNavigation " :style="`background-color: rgba(34, 34, 34, ${bopacity});`"
            ref="IndexHeadNavigation">
            <div class="w">
                <NJFUBigImg />
                <div class="right fr">
                    <ul>
                        <li>
                            <router-link active-class="active" :to="{ name: is_login ? 'Index' : 'WelcomeIndex' }">
                                </router-link>
                        </li>
                        <li>
                            <router-link active-class="active"
                                :to="{ name: is_login ? 'ProfessionalIntroduction' : 'WelcomeProfessionalIntroduction' }"></router-link>
                        </li>
                        <li>
                            <router-link active-class="active"
                                :to="{ name: is_login ? 'Teachers' : 'WelcomeTeachers' }"></router-link>
                        </li>
                        <li>
                            <router-link active-class="active"
                                :to="{ name: is_login ? 'ProfessionalUpdates' : 'WelcomeProfessionalUpdates' }"></router-link>
                        </li>
                        <li>
                            <router-link active-class="active"
                                :to="{ name: is_login ? 'ContactUs' : 'WelcomeContactUs' }"></router-link>
                        </li>
                    </ul>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import { mapState } from 'vuex'
export default {
    name: 'IndexHeadNavigation',
    data() {
        return {
            bopacity: 1
        }
    },
    computed: {
        ...mapState('userMesOptions', ['is_login'])
    },
    beforeMount() {
        this.$bus.$on('changeIndexHeadNavigationOpacity', bopacity => { this.bopacity = bopacity })
    },
    beforeDestroy() {
        if (this.$route.name !== 'Index' && this.$route.name !== 'WelcomeIndex')
            this.$bus.$off('changeIndexHeadNavigationOpacity')
    }
}
</script>

<style scoped>
.IndexHeadNavigation {
    position: absolute;
    background-color: #222;
    width: 100%;
    height: 100px;
    z-index: 1024;
}

li {
    height: 100px;
    float: left;
    color: white;
    font-size: 20px;
    padding-left: 40px;
    padding-top: 20px;
}

li:first-child {
    margin-left: 0;
}

li a {
    display: block;
    color: white;
    padding: 10px 20px;
}

.active {
    background-color: #00A78E;
}
</style>