<template>
    <!--  -->
    <div class="w">
        <h2 v-if="title">{{ title }}</h2>
        <ul v-if="files.length > 0">
            <li v-for="(f, fIndex) in files" :key="fIndex">
                <img :src="f.imgSrc || 'picture/noPicture.png'" @click="goNJFUViewerPDF(f.fileID, f.title)">
                <p>{{ f.title }}</p>
            </li>
        </ul>
        <ul v-else="files.length">
            <li>
                <img src="picture/noPicture.png">
                <p>,</p>
            </li>
        </ul>
    </div>
</template>

<script>
export default {
    name: 'FileShow',
    props: ['title', 'files']
}
</script>

<style scoped>
.w {
    overflow: auto;
}

h2 {
    margin-top: 28px;
    font-size: 20px;
}

li {
    float: left;
    margin-top: 28px;
    margin-right: 28px;
    width: 210px;
    height: 310px;
}

li img {
    width: 100%;
    height: 290px;
}

li p {
    font-size: 14px;
    text-align: center;
    line-height: 20px;
}

li:nth-child(6n) {
    margin-right: 0;
}
</style>