<template>
    <div>
        <StickyHead :title="MesReleaseTitle">
            <div style="position: relative;">
                <input type="text" v-model="searchInput">
                <div class="icon iconfont icon-search"></div>
            </div>
        </StickyHead>

        <MesPreview v-for="(m, index) in searchMes" :key="index" :title="m.title" :preview="m.preview"/>
    </div>
</template>

<script>
import Title from '../components/Title.vue';
import MesPreview from '../components/MesPreview.vue';
import StickyHead from '../components/StickyHead.vue'
export default {
    name: 'FJFUMesRelease',
    components: { Title, MesPreview, StickyHead },
    props: ['MesReleaseID', 'MesReleaseTitle'],
    data() {
        return {
            searchInput: '',
            mes: [
                { title: '1', preview: '1' },
                { title: '2', preview: '1' },
                { title: '3', preview: '1' },
                { title: '4', preview: '1' },
                { title: '5', preview: '1' },
                { title: '6', preview: '1' },
                { title: '7', preview: '1' },
                { title: '8', preview: '1' },
                { title: '9', preview: '1' },
                { title: '9', preview: '1' },
                { title: '9', preview: '1' },
                { title: '9', preview: '1' },
                { title: '9', preview: '1' },
                { title: '9', preview: '1' },
                { title: '9', preview: '1' },
                { title: '9', preview: '1' },
                { title: '9', preview: '1' }
            ]
        }
    },
    computed: {
        searchMes() {
            return this.mes.filter((p) => {
                return p.title.indexOf(this.searchInput) != -1
            })
        }
    },
    methods: {
        goNJFUViewerMes(mesID, mesTitle) {
            this.$router.push({ name: 'ViewerMes', query: { mesID, mesTitle } })
        }
    }
}
</script>

<style scoped>
input {
    background-color: #dfe0e2;
    padding: 5px 45px 5px 15px;
    height: 40px;
    width: 360px;
    border-radius: 8px;
}

.icon {
    position: absolute;
    right: 0;
    top: 0;
    height: 40px;
    width: 40px;
}
</style>