<template>
    <div class="shortcut">
        <div class="w">
            <p class="fl">
                MAKE CREATIVITY A HABIT
            </p>
            <button class="whiteGreenCursor fl">
                CONTACT US >
            </button>
            <FloatLogin ref="login" :show="login_index" :login_click_out="login_click_out" />
            <ul>
                <li class="fr" id="shortcut_box_login" @click="login_click_in">
                    <p>LOGIN HERE</p>
                </li>
            </ul>
        </div>
    </div>
</template>

<script>
import FloatLogin from '../components/FloatLogin.vue';
export default {
    name: 'HeadNavigationWelcome',
    components: { FloatLogin },
    data() {
        return {
            login_index: false
        }
    },
    methods: {
        login_click_in() {
            setTimeout(() => {
                this.$refs.login.$refs.input.focus()
            }, 500);
            this.login_index = true
        },
        login_click_out() {
            this.login_index = false
        }
    }
}
</script>

<style scoped>
.shortcut {
    min-width: 1400px;
    height: 72px;
    background-color: #222;
}

p {
    color: white;
    line-height: 72px;
}

button {
    margin-left: 15px;
    height: 40px;
    margin: 16px 0 16px 20px;
    padding: 0 15px;
    background-color: #00A78E;
    color: white;
}

p {
    text-align: center;
    line-height: 72px;
    color: white;
    letter-spacing: 0;
}

p:hover {
    cursor: pointer;
    color: orange;
}
</style>