<template>
    <div>
        <h1>{{ title }}</h1>
        <div class="left">
            <div class="icon iconfont icon-arrow-left-bold" @click="goLeft"></div>
        </div>
        <div class="middle">
            <ul>
                <transition-group :name="slideDir">
                    <li v-for="(info, index) in infos" v-show="Math.floor(index / 4) === carouselChoose" :key="index">
                        <img :src="info.imgSrc">
                        <p>{{ info.title }}</p>
                    </li>
                </transition-group>
            </ul>
        </div>

        <div class="right">
            <div class="icon iconfont icon-arrow-right-bold" @click="setCarouselChoose(carouselChoose + 1)"></div>
        </div>
    </div>
</template>

<script>
export default {
    name: 'chooseCarousel',
    data() {
        return {
            carouselChoose: 0,
            slideDir: 'slideLeft'
        }
    },
    props: ['title', 'infos'],
    methods: {
        setCarouselChoose(choose) {
            clearInterval(this.intervalID)
            if (choose < 0)
                choose = Math.ceil(this.infos.length / 4) - 1
            else if (choose > Math.ceil(this.infos.length / 4) - 1)
                choose = 0
            this.carouselChoose = choose
            this.intervalID = setInterval(this.intervalFun, this.intervalSleep)
        },
        goLeft() {
            this.slideDir = 'slideRight'
            this.setCarouselChoose(this.carouselChoose - 1)
            this.$nextTick(() => {
                this.slideDir = 'slideLeft'
            })
        }
    },
    mounted() {
        this.intervalFun = () => {
            if (this.carouselChoose === Math.ceil(this.infos.length / 4) - 1) this.carouselChoose = 0
            else this.carouselChoose++
        }
        this.intervalSleep = 5000
        this.intervalID = setInterval(this.intervalFun, this.intervalSleep)
    },
    beforeDestroy() {
        clearInterval(this.intervalID)
    }
}
</script>

<style scoped>
h1 {
    text-align: center;
    font-size: 56px;
    color: black;
    margin-bottom: 50px;
}

.middle {
    float: left;
    overflow: hidden;
    width: 80%;
    height: 400px;
}

.left {
    float: left;
    width: 10%;
}

.right {
    float: right;
    width: 10%;
}

ul {
    position: relative;
}

li {
    position: absolute;
    width: 25%;
}

li:nth-child(4n - 3) {
    left: 0%;
}

li:nth-child(4n - 2) {
    left: 25%;
}

li:nth-child(4n - 1) {
    left: 50%;
}

li:nth-child(4n) {
    left: 75%;
}

img {
    display: block;
    width: 256px;
    height: 256px;
    padding: 5px;
    border: 2px solid #dddddd;
    margin: 0 auto;
}

p {
    margin-top: 20px;
    text-align: center;
    color: black;
    font-size: 20px;
}

.icon {
    margin: 93px auto;
    font-size: 50px;
    width: 70px;
    height: 70px;
    text-align: center;
    color: white;
    line-height: 70px;
    background-color: #00A78E;
    border: 1px solid #00A78E;
    border-radius: 100%;
}

.icon:hover {
    transition: all 0.5s;
    cursor: pointer;
    background-color: white;
    color: #00A78E;
}

/* ,  */
.slideLeft-leave-to,
.slideRight-enter {
    transform: translateX(-400%);
}

.slideLeft-enter,
.slideRight-leave-to {
    transform: translateX(400%);
}

.slideRight-enter-active,
.slideRight-leave-active,
.slideLeft-enter-active,
.slideLeft-leave-active {
    transition: 0.5s linear;
}

/* ,  */
.slideLeft-enter-to,
.slideLeft-enter-to,
.slideLeft-leave,
.slideRight-leave {
    transform: translateX(0);
}
</style>