<template>
    <div class="carousel_img">
        <div class="circularsRelative w">
            <div class="circulars">
                <template v-for="(imgInfo, index) in imgInfos">
                    <div class="circular" @click="resetInterval(index)" style="background-color: #00A78E;"
                        v-show="carouselChoose === index"></div>
                    <div class="circular" @click="resetInterval(index)" v-show="carouselChoose !== index"></div>
                </template>
            </div>
        </div>

        <div class="positionRelative w">
            <div class="backAndForward">
                <div class="icon iconfont icon-arrow-left-bold fl" @click="resetInterval(carouselChoose - 1)"></div>
                <div class="icon iconfont icon-arrow-right-bold fl" @click="resetInterval(carouselChoose + 1)"></div>
            </div>
        </div>
        <transition-group name="fade">
            <template v-for="(imgInfo, index) in imgInfos">
                <PhotoCarouselOption v-show="carouselChoose === index" :key="index" :imgSrc="imgInfo.imgSrc"
                    :title="imgInfo.title" />
            </template>
        </transition-group>
    </div>
</template>
  
<script>
import PhotoCarouselOption from './PhotoCarouselOption.vue';
export default {
    name: 'PhotoCarousel',
    components: { PhotoCarouselOption },
    data() {
        return {
            carouselChoose: 0,
            imgInfos: [
                { imgSrc: 'picture/banner1.jpg', title: 'Artificial Intelligence & Its Architecture' },
                { imgSrc: 'picture/banner2.jpg', title: 'Will Artificial Intelligence ever replace humans' },
                { imgSrc: 'picture/banner3.jpg', title: 'Can machines really think?' },
                { imgSrc: 'picture/banner4.jpg', title: 'Education is a path, not a destination' }
            ]
        }
    },
    methods: {
        resetInterval(choose) {
            clearInterval(this.intervalID)
            if (choose < 0)
                choose = this.imgInfos.length - 1
            else if (choose > this.imgInfos.length - 1)
                choose = 0
            this.carouselChoose = choose
            this.intervalID = setInterval(this.intervalFun, this.intervalSleep)
        }
    },
    mounted() {
        this.intervalFun = () => {
            if (this.carouselChoose === this.imgInfos.length - 1) this.carouselChoose = 0
            else this.carouselChoose++
        }
        this.intervalSleep = 4000
        this.intervalID = setInterval(this.intervalFun, this.intervalSleep)
    },
    beforeDestroy() {
        clearInterval(this.intervalID)
    }
}
</script>

<style scoped>
.carousel_img {
    height: 650px;
}

.circularsRelative {
    position: relative;
}

.backAndForward {
    position: absolute;
    z-index: 1024;
    left: 50px;
    top: 450px;
}

.icon {
    margin-right: 30px;
    width: 50px;
    text-align: center;
    font-size: 30px;
    color: white;
    background-color: #00A78E;
}

.icon:hover {
  transition: all 0.5s;
  background-color: white;
  color: #00A78E;
}

.icon:hover {
    cursor: pointer;
}

.circulars {
    position: absolute;
    z-index: 1024;
    right: 50px;
    top: 230px;
}

.circular {
    width: 20px;
    height: 20px;
    background-color: white;
    border-radius: 100%;
    margin-bottom: 20px;
}

.circular:hover {
    cursor: pointer;
}

/* ,  */
.fade-enter,
.fade-leave-to {
    opacity: 0;
}

.fade-enter-active,
.fade-leave-active {
    transition: 1s linear;
}

/* ,  */
.fade-enter-to,
.fade-leave {
    opacity: 1;
}
</style>