<template>
    <!--  -->
    <div id="fileShow">
        <StickyHead :title="CourseResourcesTitle">
            <ul class="navigation">
                <li v-for="(i, index) in infos" :key="index" @click="ScrollIntoView(index)">{{ i.title }}</li>
            </ul>
        </StickyHead>

        <div class="box" v-for="(i, Iindex) in infos" :key="Iindex" ref="box">
            <!-- <div class="w">
                <h2>{{ i.title }}</h2>
                <ul v-if="i.files.length > 0">
                    <li v-for="(f, fIndex) in i.files" :key="fIndex">
                        <img :src="f.imgSrc || 'picture/noPicture.png'" @click="goNJFUViewerPDF(f.fileID, f.title)">
                        <p>{{ f.title }}</p>
                    </li>
                </ul>
                <ul v-else="i.files.length">
                    <li>
                        <img src="picture/noPicture.png">
                        <p>,</p>
                    </li>
                </ul>
            </div> -->
            <FileShow :title="i.title" :files="i.files"/>
        </div>
    </div>
</template>

<script>
import FileShow from '../components/FileShow.vue'
import StickyHead from '../components/StickyHead.vue'
import Title from '../components/Title.vue'
export default {
    name: 'NJFUCourseResources',
    components: {
        FileShow,
        Title,
        StickyHead
    },
    props: ['CourseResourcesID', 'CourseResourcesTitle'],
    data() {
        return {
            infos: [
                {
                    title: '', files: [
                        { title: '1', imgSrc: 'picture/banner1.jpg', fileID: '' },
                        { title: '2', imgSrc: '', fileID: '' },
                        { title: '3', imgSrc: '', fileID: '' },
                        { title: '4', imgSrc: '', fileID: '' },
                        { title: '5', imgSrc: '', fileID: '' },
                        { title: '6', imgSrc: '', fileID: '' },
                        { title: '7', imgSrc: '', fileID: '' }
                    ]
                },
                { title: '', files: [] },
                { title: '', files: [] },
                { title: '', files: [] },
                { title: '', files: [] },
                { title: '', files: [] }
            ]
        }
    },
    methods: {
        goNJFUViewerPDF(PDFID, PDFTitle) {
            this.$router.push({ name: 'ViewerPDF', query: { PDFID, PDFTitle } }, _ => { })
        },
        goNJFUViewerVideo(videoID, videoTitle) {
            this.$router.push({ name: 'ViewerVideo', query: { videoID, videoTitle } }, _ => { })
        },

        ScrollIntoView(refsIndex) {
            this.$refs.box[refsIndex].scrollIntoView({
                behavior: 'smooth',
                block: 'center'
            })
        }
    }
}
</script>

<style scoped>
.navigation li {
    float: left;
    font-size: 14px;
    padding: 0 20px;
}

.navigation li:hover {
    transition: all 0.5s;
    cursor: pointer;
    background-color: #00A78E;
    color: white;
}

.box {
    overflow: auto;
}

.box h2 {
    margin-top: 28px;
    font-size: 20px;
}

.box li {
    float: left;
    margin-top: 28px;
    margin-right: 28px;
    width: 210px;
    height: 310px;
}

.box li img {
    width: 100%;
    height: 290px;
}

.box li p {
    font-size: 14px;
    text-align: center;
    line-height: 20px;
}

.box li:nth-child(6n) {
    margin-right: 0;
}
</style>