<template>
    <!--  -->
    <div>
        <StickyHead :title="fileShowTitle">
            <div style="position: relative;">
                <input type="text" v-model="searchInput">
                <div class="icon iconfont icon-search"></div>
            </div>
        </StickyHead>
        <FileShow :files="searchFiles" />
    </div>
</template>

<script>
import axios from 'axios'
import FileShow from '../components/FileShow.vue'
import StickyHead from '../components/StickyHead.vue'
import Title from '../components/Title.vue'
export default {
    name: 'NJFUFileShow',
    components: { FileShow, Title, StickyHead },
    props: ['fileShowID', 'fileShowTitle'],
    data() {
        return {
            searchInput: '',
            files: [
                { title: '1', imgSrc: 'picture/banner1.jpg', fileID: '' },
                { title: '2', imgSrc: '', fileID: '' },
                { title: '3', imgSrc: '', fileID: '' },
                { title: '4', imgSrc: '', fileID: '' },
                { title: '5', imgSrc: '', fileID: '' },
                { title: '6', imgSrc: '', fileID: '' },
                { title: '7', imgSrc: '', fileID: '' }
            ]
        }
    },
    computed: {
        searchFiles() {
            return this.files.filter((p) => {
                return p.title.indexOf(this.searchInput) != -1
            })
        }
    },
    methods: {
        //file
        file_getJson() {
            axios.post(this.$HTTPIP + 'json/getFileJson', {
                fileShowID: this.fileShowID
            }).then(result => {
                console.log("file_getJson : ");
                console.log(result);
                this.files = result.data;
                if (this.files.file_name.length != this.files.file_url.length ||
                    this.files.file_name.length != this.files.file_picture.length) {
                    this.files = {};
                    alert('file! ');
                    return;
                }
            }).catch(function (error) {
                console.log("file_getJson : ");
                console.log(error);
                alert('file! ');
            })
        },
        goNJFUViewerPDF(PDFID, PDFTitle) {
            this.$router.push({ name: 'ViewerPDF', query: { PDFID, PDFTitle } })
        }
    },
    beforeMount() {
        // 
        if (this.fileShowID === undefined || this.fileShowTitle === undefined)
            this.$router.replace({ name: 'Index' })
    },
    mounted() {
        // console.log(this.fileShowTitle);
        // if (this.fileShowID != null)
        //     this.file_getJson();
    }
}
</script>

<style scoped>
input {
    background-color: #dfe0e2;
    padding: 5px 45px 5px 15px;
    height: 40px;
    width: 360px;
    border-radius: 8px;
}

.icon {
    position: absolute;
    right: 0;
    top: 0;
    height: 40px;
    width: 40px;
}
</style>