<template>
    <div v-if="show" @click="backTop" class="icon iconfont icon-top whiteGreenCursor"></div>
</template>

<script>
export default {
    name: 'BackToTop',
    data() {
        return {
            show: 0
        }
    },
    methods: {
        backTop() {
            document.documentElement.scrollTop = document.body.scrollTop = 0;
        }
    },
    mounted() {
        window.onscroll = _ => {
            if (window.scrollY >= 700) this.show = 1
            else this.show = 0
        }
    }
}
</script>

<style scoped>
.icon {
    position: fixed;
    right: 20px;
    bottom: 20px;
    font-size: 50px;
    width: 70px;
    height: 70px;
    background-color: #00A78E;
    border: 1px solid #00A78E;
    text-align: center;
    line-height: 80px;
    border-radius: 100%;
    color: white;
}

.icon:hover {
    cursor: pointer;
}
</style>