import axios from 'axios'
import router from '../router'

const HTTPIP = 'https://yapi.pro/mock/21525/'
// const HTTPIP = ''

// 
function setCookie(cname, cvalue, exdays) {
    var d = new Date()
    d.setTime(d.getTime() + (exdays * 24 * 60 * 60 * 1000))
    var expires = "expires=" + d.toGMTString()
    document.cookie = cname + "=" + cvalue + "; " + expires
}

const getCookie = function (cname) {
    var name = cname + "="
    var ca = document.cookie.split(';')
    for (var i = 0; i < ca.length; i++) {
        var c = ca[i].trim()
        if (c.indexOf(name) == 0) return c.substring(name.length, c.length)
    }
    return ""
}

const userMesCheck = getCookie('userMes')
const userMesCookie = userMesCheck ? JSON.parse(getCookie('userMes')) : {} //bug : json

export default {
    namespaced: true,
    actions: {
        autoLogin(context, { to }) {
            if (!context.state.TI_no || !context.state.TI_name) {
                if (to.meta.isAuth) {
                    alert(',!')
                    return router.replace({ name: 'WelcomeIndex' }, _ => { })
                }
                return router.replace({ name: to.name, query: to.query }, _ => { })
            }
            context.dispatch('login', {
                TI_no: context.state.TI_no,
                TI_password_md5: context.state.TI_password_md5,
                to
            })
        },
        login(context, { TI_no, TI_password_md5, to }) {
            if (!TI_no.trim() || !TI_password_md5.trim()) {
                alert(',!')
                router.replace({ name: 'WelcomeIndex' }, _ => { })
                return
            }
            axios.post(HTTPIP + 'json/loginSubmit', {
                task: 'account_login',
                TI_no,
                TI_password: TI_password_md5
            }).then(
                result => {
                    if (result.data.retValue == 1) {
                        context.commit('USER_MES_SET', {
                            is_login: true,
                            TI_name: result.data.TI_name,
                            TI_no,
                            TI_password_md5
                        })
                        if (!to || to.name === 'WelcomeIndex') return router.replace({ name: 'Index' }, _ => { })
                        router.replace({ name: to.name, query: to.query }, _ => { })
                    } else {
                        alert('! ,!')
                        router.replace({ name: 'WelcomeIndex' }, _ => { })
                    }
                },
                error => {
                    console.log(error);
                    alert('! !')
                    router.replace({ name: 'WelcomeIndex' }, _ => { })
                })
        },
        logout(context) {
            if (confirm('?')) {
                context.commit('USER_MES_SET', {})
                router.replace({ name: 'WelcomeIndex' }, _ => { })
            }
        }
    },
    mutations: {
        USER_MES_SET(state, { is_login, TI_no, TI_name, TI_password_md5 }) {
            state.is_login = is_login
            state.TI_no = TI_no
            state.TI_name = TI_name
            state.TI_password_md5 = TI_password_md5
            setCookie('userMes', JSON.stringify({ ...state }), 1)
        }
    },
    state: {
        is_login: false,
        TI_no: userMesCookie.TI_no,
        TI_name: userMesCookie.TI_name,
        TI_password_md5: userMesCookie.TI_password_md5
    }
}